//Imports Necesarios
@import 'custom';
@import '../../node_modules/bootstrap/scss/_functions';
@import '../../node_modules/bootstrap/scss/_variables';
@import '../../node_modules/bootstrap/scss/mixins/_breakpoints';

//Variables
$mainColor: $ripley-grey;
$backgroundColor: $ripley-white;

//mas resets necesarios
* {
    transition: all .2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    -webkit-transition: all .2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    -moz-transition: all .2s cubic-bezier(0.175, 0.885, 0.32, 1.275);
    -o-transition: all .2s cubic-bezier(0.175, 0.885, 0.32, 1.275); }
::-moz-selection {
    @include selection( $ripley-red, $ripley-white); }
::selection {
    @include selection( $ripley-red, $ripley-white); }
.wow {
    visibility: hidden;
    z-index: 9;
    &:hover {
        z-index: 99; } }
.btn {
    min-width: 100px;
    &:hover {
        //@include shadow4()
        z-index: 1; } }
.btn-outline-ripley-white:hover {
    color: $ripley-grey !important; }
.modal {
    z-index: 4050 !important;
    overflow: scroll !important; }
.card {
    @include shadow-4dp();
    @include border-radius(5);
    transition: all 0.3s cubic-bezier(.25,.8,.25,1);
    overflow: hidden;
    z-index: 9;
    &:hover {
        z-index: 99;
        @include shadow-8dp(); } }
//Basicos
@import '_utilidades/precios';
p {
    margin: 0; }

